<template>
    <div>
         <div class="contact">
        <div class="contact_con">
            <div class="map" style="border:1px solid #ccc;">
                <div id="container"></div>
            </div>
            <p class="contact_title">
                如果您有合作意向，请通过以下 <br>方式联系我
            </p>
            <ul class="contact_detail">
                <li style="font-weight: bold">联系方式：</li>
                <li>邮箱 - yc792819924@163.com&nbsp;&nbsp;&nbsp;&nbsp;QQ - 792819924&nbsp;&nbsp;&nbsp;&nbsp;微信 - xxicao </li>
                <li>添加好友请标明备注，谢谢理解！</li>
                <li>您有任何宝贵的修改建议或者合作意向都可以投递至我的邮箱，我会及时回复！</li>
                <li>您还可以通过下方的留言面板与我进行联系。</li>
            </ul>
            <form>
                <div class="contact_talk">
                    <div class="contact_talk_left">
                        <input type="text" v-model="forms.username"   placeholder="姓名" required style="margin-top: 0">
                        <input type="email" v-model="forms.useremail" required placeholder="email">
                        <div style="height: 220px;border:1px solid #ddd;background-color: #ecf0f1;margin-top: 20px">
                            <textarea name="usermsg" v-model="forms.usermsg" id="contact_area" required placeholder="我想说..."></textarea>
                        </div>

                    </div>
                    <div class="contact_talk_right">
                        <p class="contact_tr_title">留言板</p>
                        <div style="width: 340px;overflow: hidden">
                            <div class="contact_tr_con" id="contact_tr_con">
                                <ul class="contact_tr_list" id="contact_tr_list">
                                    <li>
                                        <p>姓名: <span>小艾</span><var>email: yc792819924@163.com</var><em></em></p>
                                        <div>不错不错！</div>
                                    </li>
                                    <li>
                                        <p>姓名: <span>测试</span><var>email: 792819924@qq.com</var><em></em></p>
                                        <div>看好你！</div>
                                    </li>
                                    <li>
                                        <p>姓名: <span>测试123</span><var>email: 123@qq.com</var><em></em></p>
                                        <div>看好你123！</div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <p class="contact_btn"><button type="button" @click="submitMsg()">留言</button></p>
                </div>
            </form>
        </div>
    </div>
    </div>
</template>
<script>
import Axios from 'axios'
export default {
    data(){
        return {
            forms: {
                username: null,
                useremail: null,
                usermsg: null,
            }
           
        }
    },
    methods:{
        submitMsg(){
            var api = 'http://www.xxicao.com/contact/submit';
            var pattern=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            if(!this.forms.username || !this.forms.username.trim()) {
				alert('请填写昵称');
				return false;
			}
			if(!this.forms.useremail || !this.forms.useremail.trim()) {
				alert('请填写邮件地址');
				return false;
            }
            if(!pattern.test(this.forms.useremail.trim())) {
				alert('邮件地址格式错误');
				return false;
			}
			if(!this.forms.usermsg || !this.forms.usermsg.trim()) {
				alert('请填写留言');
				return false;
			}
            Axios.post(api,this.forms).then((res) => {
                if(res.data) {
                    alert(res.data.msg);
                    this.forms = {
                        username: null,
                        useremail: null,
                        usermsg: null,
                    }
                }
               
            }).catch((err) => {
                console.log(err);
            })
        }
    },
    mounted(){
        var map = new BMap.Map("container");
		var point = new BMap.Point(121.405649,31.211729);
		map.centerAndZoom(point,19);
		// 创建地址解析器实例
		var myGeo = new BMap.Geocoder();
		map.enableScrollWheelZoom(true); 
		// 将地址解析结果显示在地图上,并调整地图视野
		myGeo.getPoint("上海市绿地双创中心", function(point){
			if (point) {
				map.centerAndZoom(point, 19);
				map.addOverlay(new BMap.Marker(point));
			}else{
				alert("您选择地址没有解析到结果!");
			}
		}, "上海市");
    }
}
</script>
